        .server-box,
        .info-box {
            position: absolute;
            left: 28px;
            color: #fff;
            z-index: 2;
        }
        .server-box {
            display: flex;
            top: 23px;
            gap: 0 5px;
            font-size: 1.3rem;
            p {
                padding: 5px 7px;
                background: rgba(255, 255, 255, 0.2);
                border-radius: 6px;
            }
        }
        .info-box {
            left: 17px;
            bottom: 27px;
            .info-title {
                display: flex;
                width: 250px;
                text-align: left;
                margin-bottom: 3%;
                .sub-title {
                    flex: 1;
                    font-weight: 600;
                    text-align: center;
                    font-size: medium;
                    color: rgba(255, 255, 255, 0.8);
                }
            }
            .info-level {
                display: flex;
                width: 235px;
                text-align: center;
                margin-bottom: 3%;
                .level {
                    flex: 1;
                    font-weight: 600;
                    font-size: medium;
                    color: rgba(255, 255, 255, 0.8);
                }
            }
            .info-name {
                display: flex;
                text-align: left;
                .name {
                    flex: 1;
                    font-size: 2.5rem;
                    font-weight: 700;
                    margin-left: 10px;
                }
                .title {
                    font-size: 1.4rem;
                    font-weight: 300;
                    opacity: 0.7;
                    padding-top: 6px;
                    margin-left: 7px;
                }
            }
        }
        .img-box {
            position: relative;
            height: 280px;
            border-radius: 8px;
            background: #15181d;
            overflow: hidden;
            .effect {
                position: absolute;
                top: -80%;
                left: -20%;
                mix-blend-mode: screen;
                width: 79rem;
                height: 51rem;
                transition: all 1s ease-out 0s;
                mask-image: radial-gradient(
                    50% 50% at 50% 50%,
                    rgb(255, 255, 255) 0%,
                    rgba(255, 105, 0, 0) 100%
