@media (max-width: 1200px) {
    .character {
        &-inner {
            gap: 0 30px;
        }
        &-area {
            width: 90%;
        }
    }

    .profile {
        .img-box {
            height: 250px;
        }
    }
}

@media (max-width: 900px) {
    .character {
        &-inner {
            flex-direction: column;
            gap: 20px;
        }
        &-area {
            width: 100%;
            order: 2;
        }
        &-search-area {
            order: 1;
        }
    }

    .profile {
        .img-box {
            height: 200px;
        }
    }

    .equip {
        .equipment {
            width: 100%;
        }
    }
}

@media (max-width: 600px) {
    .profile {
