        }
        .card-setinfo {
            &-inner {
                display: grid;
                width: 512;
                gap: 0px;
                grid-template-columns: repeat(2, 100px);
                .card-setinfo-name {
                    font-size: 8px;
                    font-family: 'jua';
                    font-weight: 550;
                }
                .card-setinfo-des {
                    font-size: 8px;
                    font-family: 'jua';
                    font-weight: 550;
                }
            }
        }

        .skill-main {
            padding-left: 5px;
            padding-right: 5px;
            &-inner {
                display: flex;
                flex-direction: column;
                gap: 10px;
            }
        }
        .skill-sub {
            display: flex;
            flex-direction: column;
            // display: flex;
            // flex-direction: row;
            // position: relative;
            // width: 100%;
            // border-bottom: 1px solid #d3d3d3b5;
            // margin-bottom: 15px;
            // gap: 10px;
            &-info {
                height: 40px;
                border: none;
                display: grid;
                grid-template-columns: 1fr 2fr 1fr;
                width: 160px;
                img {
                    width: 23px;
                    height: 23px;
                }
                &-text {
                    &-level {
                        font-size: 10px;
                        span {
                            font-size: 10px;
                        }
                    }
                    &-name {
                        font-size: 10px;
                    }
                }
                &-detail {
                    width: 50px;
                    div {
                        font-size: 7px;
                    }
                }
            }
            &-tripods {
                order: 2;
                border: none;
                display: grid;
                grid-template-columns: repeat(3, 75px);
                padding-left: 5px;
                margin-top: 9px;
                &-inner {
                    img {
                        width: 20px;
                        height: 20px;
                    }
                    &-info {
                        &-level {
                            font-size: 9px;
                            span {
                                font-size: 9px;
                            }
                        }
                        &-name {
                            font-size: 8px;
                        }
                    }
                }
            }
            &-subinfo {
                order: 1;
                position: absolute;
                // top: -3px;
                right: 5px;
                display: flex;
                flex-direction: row;
                flex: 0.5 1;
                gap: 5px;
                height: 30px;
                // align-items: center;
                img {
                    width: 13px;
                    height: 13px;
                }
                .empty {
                    width: 13px;
                    height: 13px;
                }
                &-rune {
                    // padding-top: 2px;
                    // align-items: center;
                    // text-align: center;
                    &-name {
                        font-size: 8px;
                    }
                }
            }
            &::after {
                content: '';
                position: absolute;
                bottom: -7px;
                left: 2%;
                width: 95%;
                height: 1px;
                background-color: #d3d3d3b5;
            }
        }
    }
    .equip {
        .equipment {
            font-size: 14px;
        }
    }

    .stats {
        .stats-info,
        .sub-stats,
        .ark,
        .char-stats {
            // font-size: 14px;
            display: flex;
            flex-direction: column;
            gap: 10px;
            p {
                font-size: 13px;
            }
        }
        .engrav {
            padding: 0px;
            .engrav-item {
                .engravname {
                    font-family: 'jua';
                    font-size: 10px;
                }
            }
        }
    }
}
